import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, CanActivate, CanActivateChild, Router, RouterStateSnapshot, UrlTree} from '@angular/router';
import {Observable} from 'rxjs';
import {AuthService} from '../_services/auth.service';
import {TokenStorageService} from '../_services/token-storage.service';

@Injectable({
  providedIn: 'root'
})
export class AuthGuard implements CanActivate, CanActivateChild {

  constructor(private authService: AuthService, private router: Router, private tokenStorageService: TokenStorageService) {
  }

  canActivate(
    next: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
  
    const currentUser = this.tokenStorageService.getUser();
  
    if (currentUser) {
      const userRoles = currentUser.roles; // mng v d: ['ROLE_ADMIN', 'ROLE_STUDENT']
      const requiredRoles = next.data.roles; // mng yu cu t routing
  
      // Nu route yu cu quyn v ngi dng khng c quyn no khp
      if (requiredRoles && !requiredRoles.some(role => userRoles.includes(role))) {
        this.router.navigate(['/']);
        return false;
      }
  
      return true;
    }
  
    // Cha ng nhp
    this.router.navigate(['/login'], { queryParams: { returnUrl: state.url } });
    return false;
  }
  

  // tslint:disable-next-line:max-line-length
  canActivateChild(childRoute: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean | UrlTree | Observable<boolean | UrlTree> | Promise<boolean | UrlTree> {
    return this.canActivate(childRoute, state);
  }
}
